.progress-area {
  bottom: 20px;
  right: 40px;
  position: absolute;
  align-self: flex-end;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 160px;
  height: 200px;
  color: gray; }
  .progress-area p {
    width: 125px;
    z-index: 2;
    text-align: center; }
