    font-weight: 600;
    color: #41af79; }

.progress-chart {
  width: 150px;
  height: 150px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  position: absolute;
  background: #e6e6e6 linear-gradient(to right, #e6e6e6 50%, #5fc492 50%);
  color: #5fc492;
  border-radius: 50%; }

@keyframes spin {
  to {
    transform: rotate(180deg); } }

@keyframes background {
  50% {
    background-color: currentColor; } }
  .progress-chart::after {
    content: '';
    position: absolute;
    width: 80%;
    height: 80%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    background: white;
    border-radius: 50%; }
  .progress-chart::before {
    content: '';
    position: absolute;
    display: block;
    background-color: inherit;
    height: 100%;
    width: 50%;
    bottom: 0;
    right: 0;
    border-radius: 0 100% 100% 0 / 50%;
    transform: rotate(0);
    transform-origin: 0 50%;
    animation: 50s spin infinite linear, 100s background infinite step-end;
    animation-play-state: paused;
    animation-delay: inherit; }

